<template>
  <div class="contents_container_inner">
    <!-- <div class="contents_title">
      <h3>힐링 활동을 소개합니다.</h3>
    </div>   -->
    <div class="contents_container_inner">
      <div class="contents_item">
        <LineChart  :chartData="lineChartData"
                    :chartOptions="lineChartOptions"
                    :width="300"
                    :height="300">
        </LineChart>
      </div>
      <div class="contents_item">
        <BarChart   :chartData="lineChartData"
                    :chartOptions="lineChartOptions"
                    :width="300"
                    :height="300">
        </BarChart>
      </div>
      <div class="contents_item">
        <DoughnutChart  :chartData="gaugeChartData"
                        :chartOptions="gaugeChartOptions"
                        :width="300"
                        :height="300">
        </DoughnutChart>
      </div>
    </div>
    <div class="contents_item_data">
      <table>
        <tr>
          <th style="width: 10%">id</th>
          <th style="width: 50%">contents</th>
          <th style="width: 10%">feeling_val</th>
          <th style="width: 30%">insert_dttm</th>
        </tr>
        <tr v-for="(feelingItem,id) in this.resultJson" :key="id">
          <td style="width: 10%">{{feelingItem.id}}</td>
          <td style="width: 50%">{{feelingItem.contents}}</td>
          <td style="width: 10%">{{feelingItem.feeling_val}}</td>
          <td style="width: 30%">{{feelingItem.insert_dttm}}</td>
        </tr>
      </table>
    </div>

    <div class="contents_container_inner">
      <input type="file" @change="onFileSelected($event)" ref="fileInput" />
      <button v-on:click="imageSave()">일기저장</button>
    </div>




    <input style="display: none" type="file" @change="onFileSelected($event)" ref="fileInput2" />
    <div style="width:95%; margin:auto;">
        <img :src="imageSrc" v-if="imageSrc" style="width: 200px; height: 200px; object-fit: contain;"/><br>
        <button @click="$refs.fileInput2.click">Image Pick</button>
        <button v-on:click="myPlantProfileImageUpdate()">사진수정</button>
    </div>
    
  </div>
</template>
  
<script>
const axios = require("axios");
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);
import LineChart from './../chart/LineChart.vue';
import BarChart from './../chart/BarChart.vue';
import DoughnutChart from './../chart/DoughnutChart.vue';

export default {
  props: [], 
  components: {
    LineChart,
    BarChart,
    DoughnutChart,
  },
  data() {
    return {
      lineChartData: {},
      lineChartOptions : {},
      gaugeChartData: {},
      gaugeChartOptions : {},
      dataHealingActivity: healingActivityJson,
      healingActivity: [],
      showContentYn: false,
      currentPage: 1,
      postsPerPage: 10,

      imageSrc: null,
      imgDiaryCntnts: null,
      selectedFile: null,
      resultJson: {},
      returnMsg: "",
      tmpResponse: {}
    };
  },
  computed: {
    displayedPosts() {
      const startIndex = (this.currentPage - 1) * this.postsPerPage;
      const endIndex = startIndex + this.postsPerPage;
      return this.dataHealingActivity.slice(startIndex, endIndex);
    },
    totalPages() {
      return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
    }
  },
  created() {
    this.setLineChartData();
    this.setGaugeChartData();
    this.getItemList()
  },
  methods: {
    setLineChartData() {
      var chartDataLabels = [];
      var chartDataDatas = [];
      // var chartBackgroundColor = [];
      
      // var chartDataLabels = ['Baddy', 'Sosoy', 'Goody'];
      // var chartDataDatas = [5, 4, 12];
      // var chartBackgroundColor = [
      //   'rgb(192, 0, 0)',
      //     'rgb(253, 230, 89)',
      //     'rgb(67, 179, 52)',
      // ];

      axios
        .post("/api/item", {calc: "Y"})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {  
          // console.log('extractSqlResult response : ', response.data);
          // this.tmpResponse = response;
          this.returnMsg = response.data.returnMsg;
          this.resultJson = response.data.resultJson;
          
          for(var i = 0; i < this.resultJson.length; i++) {
            chartDataLabels.push(this.resultJson[i].insert_dt);
            chartDataDatas.push(this.resultJson[i].write_cnt);
          }
          // console.log("chartDataLabels >>> ", chartDataLabels)
          // console.log("chartDataDatas >>> ", chartDataDatas)

          // chartDataLabels = [1, 2, 3, 4, 5, 6, 7]
          // chartDataDatas = [65, 59, 80, 81, 56, 55, 40]

          this.lineChartData = {
            labels:chartDataLabels, 
            datasets:[{
              label: 'WriteCount/Day',
              data: chartDataDatas, 
              scales: {
                y: {
                    stacked: true
                }
              },
              // backgroundColor: chartBackgroundColor,
              // borderWidth: 1
              fill: false,
              borderColor: 'rgb(75, 192, 192)',
              tension: 0.1
            }]
            },
          this.lineChartOptions = {
            maintainAspectRatio: true,   // 비율고정 해제
          }
        })
        .catch((error) => {
          console.log(error);
        });
    }, 
    setGaugeChartData() {
      var chartDataLabels = ['Baddy', 'Sosoy', 'Goody'];
      var chartDataDatas = [5, 4, 12];
      var chartBackgroundColor = [
        'rgb(192, 0, 0)',
          'rgb(253, 230, 89)',
          'rgb(67, 179, 52)',
      ];

      this.gaugeChartData = {
        labels:chartDataLabels, 
        datasets:[
          {
            label: '키워드분류건수',
            data: chartDataDatas, 
            backgroundColor: chartBackgroundColor,
            borderWidth: 1
                  }
                ]
        },
      this.gaugeChartOptions = {
        maintainAspectRatio: true,   // 비율고정 해제
        rotation: -90,
        circumference: 180,
        legend: {
          display: false,
        },

      }
    }, 
    getItemList () {
      console.log('in getItem List...');
      axios
        .get("/api/item", {})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {  
          // console.log('extractSqlResult response : ', response);
          this.tmpResponse = response;
          this.returnMsg = response.data.returnMsg;
          this.resultJson = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    myPlantProfileImageUpdate(){
      var cltvtActJson = {};
      // var cltvtActJsoArray = [];
      
      // 현재 날짜와 시간을 가져오기
      const currentDate = new Date();

      // 각 구성 요소를 가져오기
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth() + 1;
      const day = currentDate.getDate();
      const hours = currentDate.getHours();
      const minutes = currentDate.getMinutes();
      const seconds = currentDate.getSeconds();

      // 날짜와 시간을 문자열로 포맷팅
      const formattedDate = `${year}${String(month).padStart(2, '0')}${String(day).padStart(2, '0')}_${String(hours).padStart(2, '0')}${String(minutes).padStart(2, '0')}${String(seconds).padStart(2, '0')}`;




      // 1. 사용자 입력 내용 확인
      cltvtActJson = {
          // cltvtId: this.cltvtId,
          postRegDttm: formattedDate,
          postRegTitle: 'healing활동테스트',
          postRegContent: '내용테스트입니다.',
      };

      // 2. 반려식물 이미지 
      const formData = new FormData();
      formData.append("profileYn", "Y");
      formData.append("image", this.selectedFile);
      formData.append("cltvtActJson", JSON.stringify(cltvtActJson));
      
      axios
          .post("/api/file/uploadImage", formData)
          .then((response) => {
              console.log(response.status);
              alert('반려식물 프로필 사진이 변경되었습니다.');
              this.modalClose(true);
          })
          .catch((err) => {
              console.log("uploadImage post error");
              console.log(err);
          });

          // window.location.href = "/menu2_3";
  },
    onFileSelected: function (e) {
      // 1. 첨부할 파일을 가져옴.
      this.selectedFile = e.target.files[0];
      ///// validation (이미지인지? 사이즈 제한? )

      // 2. 파일을 base64 로 전환
      var reader = new FileReader();
      reader.readAsDataURL(this.selectedFile);

      // 3. 읽은 파일을 소스단에 출력
      reader.onload = () => {
          // console.log(reader.result);   // base64 object
          this.imageSrc = reader.result;
      };
      // this.$emit('input', this.selectedFile)
      console.log('selectedFile >>>>>>>> ', this.selectedFile);
    },
    imageSave(){
      // 2. 반려식물 이미지 
      const formData = new FormData();
      formData.append("image", this.selectedFile);
      console.log('selectedFile >>>>>>>> ', this.selectedFile);
      
      axios
            .post("/api/file", {formData: formData})
            // axios.post(this.router, resultJsonImp)
            .then((response) => {  
              console.log('extractSqlResult response : ', response);
            })
            .catch((error) => {
              console.log(error);
            });

          // window.location.href = "/menu2_3";
  },
    showContent(healingActivity) {
      this.healingActivity = healingActivity;
      this.showContentYn = true;
    },
  },
};
</script>