  <div class="contents_container_inner">
    <div class="contents_item_category">
      <div class="contents_title">
        Lab Menu
      </div>
      <div class="contents_container_inner">
        <div class="modal_menu_container">
          <!-- <div class="modal_left"> -->
            <!-- <div class="modal_menu_body"> -->
              <div class="modal_menu" v-for="(menuLabItem,id) in menuLabItem" :key="id">
                <div class="top_menu_element">
                  <!-- <router-link class="router_link" v-on:click="setMenu(id, -1, menuItem.names, menuItem.router, '', '');" 
                              :to="{path:menuLabItem.router}">
                      {{menuLabItem.names}}
                  </router-link> -->
                  <router-link  class="router_link" :to="{path:menuLabItem.router, query: {explain: menuLabItem.explain}}" 
                                  v-on:click="setMenu(id, -1, menuLabItem.names, menuLabItem.router, '', '');">
                      {{menuLabItem.names}}
                  </router-link>
                </div>
                <div class="modal_sub_menu_element">
                  <div v-for="(subItem,sub_id) in menuLabItem.subMenuItem" :key="sub_id">
                    <router-link  class="router_link_sub" :to="{path:subItem.router}"
                                v-on:click="setMenu(id, sub_id, menuLabItem.names, menuLabItem.router, subItem.names, subItem.router);">
                    {{subItem.names}}
                    </router-link>
                  </div>
                </div>
              </div>
            <!-- </div> -->
          <!-- </div> -->
        </div>
      </div>
    </div>
    <div class="contents_item_right">
      <div class="contents_container_inner" style="border: 1px solid black">
        abcde
        <transition name="fade" mode="out-in">
          <router-view name="plantMain"/>
        </transition>
      </div>
    </div>
  </div>
  <!-- <div class="contents_container_inner">
    <div class="main_banner">
      식물 소개
    </div>
    <div class="contents_container_inner" style="border: 0px solid lightgray;">
      <router-link v-on:click="this.showRouterViewYn = true;" :to="{ path: '/plantIntro' }">
          plantIntro
        </router-link>
    </div>
    <div class="contents_container_inner" v-if="this.showRouterViewYn">
      <transition name="fade" mode="out-in">
        <router-view />
