    </div>
  </div> -->
</template>
  
<script>
import dataMenu from './../../assets/json/menu.json';  // select * from get_result_json(25, 1) as get_result_json;

// const axios = require("axios");
export default {
  name: 'LabMain',
  props: {
  },
  components: {
  },
  data() {
    return {
      menuItemData: dataMenu,
      
      menu: true, 
      menu_admin: false,

      // 사용자 우선
      // menu: true, 
      // menu_admin: false,
      
      // 관리자 우선
      // menu: false, 
      // menu_admin: true,

      // // manu
      // menuId: 0, 
      // id: 0, 
      // subId: 0, 
      
      menuAdminItem: [],
      menuItem: [],
      menuLabItem: [],
    }
  },
  created() {
  },
  beforeMount() {
    this.getMenuAuth();
  },
  methods: {
    topFunction() {
      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
    },
    async getMenuAuth() {
      var resultJson;

      resultJson = this.menuItemData;

      this.menuAdminItem = resultJson[0][0].result_json;
      this.menuItem = resultJson[0][1].result_json;
      this.menuLabItem = resultJson[0][2].result_json;
    },
    showMenu(){
      // alert('showMenu');
      this.menu=true;
      this.menu_admin=false;
    }, 
    showMenuAdmin(){
      // alert('showMenuAdmin');
      this.menu=false;
      this.menu_admin=true;
      this.openModal = false;
    },
    setMenu(id, subId, selectedMainMenuNames, selectedMainMenuRouter, selectedSubMenuNames, selectedSubMenuRouter){
      this.id = id;
      this.subId = subId;
      // console.log('menu clicked!!', selectedMainMenuNames, ' > ', selectedSubMenuNames);
      this.$emit('menuClicked', selectedMainMenuNames, selectedMainMenuRouter, selectedSubMenuNames, selectedSubMenuRouter);

      this.openModal = false;
    },

    
    openModalView() {
      this.openModal = true;
    },
    closeModalView() {
      this.openModal = false;
    },
  }
}
</script>