<template>
  <canvas ref="lineChart"></canvas>
</template>

<script>
// import { Line } from 'vue-chartjs';
import { Chart as ChartJS, Title, Tooltip, Legend, ArcElement, LineController, LineElement, PointElement, CategoryScale, LinearScale } from 'chart.js';
// import { Chart, ChartConfiguration, LineController, LineElement, PointElement, LinearScale, Title} from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, ArcElement, LineController, LineElement, PointElement, CategoryScale, LinearScale);
// Chart.register(LineController, LineElement, PointElement, LinearScale, Title);


let myChart;
export default {
  // components: {
  //   Line,
  // },
  props: {
    chartData: Object,
    chartOptions: Object,
    width: Number, 
    height: Number,
    // width: {
    //   type: Number,
    //   default: 200
    // }, 
    // height: {
    //   type: Number,
    //   default: 200
    // }
  },
  mounted() {
      this.renderChart();
  }, 
  methods: {
    renderChart() {
      myChart = new ChartJS(this.$refs.lineChart, 
                            { 
                              type: 'line', 
                              data: this.chartData, 
                              // options: this.chartOptions,
                              options: this.chartOptions,
                              width: this.width,
                              height: this.height,
                              position: 'relative'
                            });
    },
    updateChart() {
      myChart.destroy();
      this.renderChart();
      // myChart.update();
    },
  },
  updated() {
    this.updateChart();
  },
}
</script>
