<template>
  <div class="contents_container_inner">
    <!-- <div class="contents_container_inner_70">
      <div class="contents_item"> -->
        <!-- {{ gaugeChartData }} (-1 ~ +1) -->
    <div class="contents_container_inner_70" style="height: 20rem;">
      <div class="contents_title">
        emotion value/Month
      </div>
      <div class="contents_container_inner_50">
        <div>
          <DoughnutChart  :chartData="gaugeChartData"
                          :chartOptions="gaugeChartOptions"
                          :width="100"
                          :height="50">
          </DoughnutChart>
        </div>
      </div>
    </div>
    <div class="contents_container_inner_70" style="height: 20rem">
      <div class="contents_container_inner_50">
        <div class="contents_title">
          Daily Emotion Value Trend
        </div>
        <LineChart  :chartData="lineChartData"
                    :chartOptions="lineChartOptions"
                    :width="100"
                    :height="50">
        </LineChart>
      </div>
      <div class="contents_container_inner_50">
        <div class="contents_title">
          Daily Write Count
        </div>
        <BarChart   :chartData="barChartData"
                    :chartOptions="barChartOptions"
                    :width="100"
                    :height="50">
        </BarChart>
      </div>
    </div>
    <div class="contents_container_inner_70">
      <div class="contents_item_data">
        <table style="">
          <tr>
            <!-- <th style="width: 10%">id</th>
            <th style="width: 50%">contents</th>
            <th style="width: 10%">feeling_val</th>
            <th style="width: 30%">insert_dttm</th> -->
            <th>id</th>
            <th>contents</th>
            <th>feeling_val</th>
            <th>insert_dttm</th>
          </tr>
          <tr v-for="(feelingItem,id) in this.resultJson" :key="id">
            <!-- <td style="width: 10%">{{feelingItem.id}}</td>
            <td style="width: 50%">{{feelingItem.contents}}</td>
            <td style="width: 10%">{{feelingItem.feeling_val}}</td>
            <td style="width: 30%">{{feelingItem.insert_dttm}}</td> -->
            <td>{{feelingItem.id}}</td>
            <td>{{feelingItem.contents}}</td>
            <td>{{feelingItem.feeling_val}}</td>
            <td>{{feelingItem.insert_dttm}}</td>
          </tr>
        </table>
      </div>
    </div>
    <div class="contents_container_inner_70">
      <input type="file" @change="onFileSelected($event)" ref="fileInput" />
      <button v-on:click="imageSave()">일기저장</button>
    </div>
    <div class="contents_container_inner_70">
      <input style="display: none" type="file" @change="onFileSelected($event)" ref="fileInput2" />
      <div style="width:95%; margin:auto;">
          <img :src="imageSrc" v-if="imageSrc" style="width: 200px; height: 200px; object-fit: contain;"/><br>
          <button @click="$refs.fileInput2.click">Image Pick</button>
          <button v-on:click="myPlantProfileImageUpdate()">사진수정</button>
      </div>
    </div>
  </div>
</template>
  
<script>
const axios = require("axios");
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);
import DoughnutChart from './../chart/DoughnutChart.vue';
import LineChart from './../chart/LineChart.vue';
import BarChart from './../chart/BarChart.vue';

export default {
  props: [], 
  components: {
    DoughnutChart,
    LineChart,
    BarChart,
  },
  data() {
    return {
      lineChartData: {},
      lineChartOptions : {},
      barChartData: {},
      barChartOptions : {},
      gaugeChartData: {},
      gaugeChartOptions : {},
      dataHealingActivity: healingActivityJson,
      healingActivity: [],
      showContentYn: false,
      currentPage: 1,
      postsPerPage: 10,

      imageSrc: null,
      imgDiaryCntnts: null,
      selectedFile: null,
      resultJson: {},
      returnMsg: "",
      tmpResponse: {}
    };
  },
  computed: {
    displayedPosts() {
      const startIndex = (this.currentPage - 1) * this.postsPerPage;
      const endIndex = startIndex + this.postsPerPage;
      return this.dataHealingActivity.slice(startIndex, endIndex);
    },
    totalPages() {
      return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
    }
  },
  created() {
    this.getDailyChartData();
    // this.setGaugeChartData();
    // this.setEValueChartData();
    // this.setECountChartData();
    this.getItemList()
  },
  methods: {
    getDailyChartData() {
      // var chartBackgroundColor = [];
      // var returnMsg = {};
      var resultJson = {};
      var chartData = {};
      axios
        .post("/api/item", {dml: 'select', userId: 1, calc: "Y"})
        .then((response) => {  
          // console.log('extractSqlResult response : ', response.data);
          // this.tmpResponse = response;
          
          // this.returnMsg = response.data.returnMsg;
          // chartDataResultJson = response.data.resultJson;
          resultJson = response.data;
          // resultJson[0].get_result_json[0].emotionAvgLastMonth
          // resultJson[0].get_result_json[0].emotion_val_trend_daily
          // resultJson[0].get_result_json[0].emotion_cnt_daily
          chartData = resultJson[0].get_result_json[0]
          // for(var i = 0; i < resultJson.length; i++) {
            // chartDataLabels.push(resultJson[i].insert_dt);
            // chartDataDatas.push(resultJson[i].emotion_val_trend_daily);
          //   console.log("resultJson >>> ", resultJson)
          // }

          this.setGaugeChartData(chartData.emotionAvgLastMonth);
          this.setEValueChartData(chartData.emotion_val_trend_daily);
          this.setECountChartData(chartData.emotion_cnt_daily);
        })
        .catch((error) => {
          console.log(error);
        });
    }, 
    setGaugeChartData(chartData) {
      console.log('chartData : ', chartData)
      
      var chartDataLabels = ['Baddy', 'Sosoy', 'Goody'];
      var chartDataDatas = [5, 4, 12];
      var chartBackgroundColor = [
        'rgb(192, 0, 0)',
          'rgb(253, 230, 89)',
          'rgb(67, 179, 52)',
      ];

      this.gaugeChartData = {
        labels:chartDataLabels, 
        datasets:[
          {
            label: '키워드분류건수',
            data: chartDataDatas, 
            backgroundColor: chartBackgroundColor,
            borderWidth: 1
                  }
                ]
        },
      this.gaugeChartOptions = {
        maintainAspectRatio: true,   // 비율고정 해제
        rotation: -90,
        circumference: 180,
        legend: {
          display: false,
        },

      }
    }, 
    setEValueChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];
          
      for(var i = 0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].insert_dt);
        chartDataDatas.push(chartData[i].chart_data);
      }

      this.lineChartData = {
        labels:chartDataLabels, 
        datasets:[{
          label: 'emotionValue/Day',
          data: chartDataDatas, 
          scales: {
            y: {
                stacked: true
            }
          },
          backgroundColor: 'rgb(75, 192, 192)',
          // borderWidth: 3,
          fill: false,
          borderColor: 'rgb(75, 192, 192)',
          tension: 0.1
        }]
      }
      this.lineChartOptions = {
                            scales: {
                              x: {
                                grid: {
                                  display: true
                                },
                                ticks: {
                                  padding: 1
                                }
                              },
                              y: {
                                grid: {
                                  drawBorder: false,
                                  color: "lightgray",
                                  lineWidth: 1
                                },
                                min: -1,
                                max: 1,
                                // ticks: {
                                //   stepSize: 100,
                                //   padding: 10,
                                // }
                              }
                            },
                            maintainAspectRatio: true,   // 비율고정 해제
                          }
    }, 
    setECountChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];

      for(var i = 0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].insert_dt);
        chartDataDatas.push(chartData[i].chart_data);
      }
      console.log('setECountChartData : chartDataLabels : ', chartDataLabels)
      console.log('setECountChartData : chartDataDatas : ', chartDataDatas)

      this.barChartData = {
          labels:chartDataLabels, 
          datasets:[{
            label: 'WriteCount/Day',
            data: chartDataDatas, 
            scales: {
              y: {
                  stacked: true
              }
            },
            // backgroundColor: chartBackgroundColor,
            // borderWidth: 1
            fill: false,
            borderColor: 'rgb(75, 192, 192)',
            tension: 0.1
          }]
          }
        this.barChartOptions = {
                              scales: {
                                x: {
                                  grid: {
                                    display: true
                                  },
                                  ticks: {
                                    padding: 1
                                  }
                                },
                                y: {
                                  grid: {
                                    drawBorder: false,
                                    color: "lightgray",
                                    lineWidth: 1
                                  },
                                  min: 0,
                                  // max: 10,
                                  // ticks: {
                                  //   stepSize: 100,
                                  //   padding: 10,
                                  // }
                                }
                              },
                              maintainAspectRatio: true,   // 비율고정 해제
          }
    }, 
    getItemList () {
      axios
        .post("/api/item", {dml: 'select', userId: 1})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {  
          this.tmpResponse = response;
          this.returnMsg = response.data.returnMsg;
          this.resultJson = response.data;

          // console.log('getItemList : this.resultJson : ', this.resultJson)
        })
        .catch((error) => {
          console.log(error);
        });
    },
    myPlantProfileImageUpdate(){
      var cltvtActJson = {};
      // var cltvtActJsoArray = [];
      
      // 현재 날짜와 시간을 가져오기
      const currentDate = new Date();

      // 각 구성 요소를 가져오기
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth() + 1;
      const day = currentDate.getDate();
      const hours = currentDate.getHours();
      const minutes = currentDate.getMinutes();
      const seconds = currentDate.getSeconds();

      // 날짜와 시간을 문자열로 포맷팅
      const formattedDate = `${year}${String(month).padStart(2, '0')}${String(day).padStart(2, '0')}_${String(hours).padStart(2, '0')}${String(minutes).padStart(2, '0')}${String(seconds).padStart(2, '0')}`;




      // 1. 사용자 입력 내용 확인
      cltvtActJson = {
          // cltvtId: this.cltvtId,
          postRegDttm: formattedDate,
          postRegTitle: 'healing활동테스트',
          postRegContent: '내용테스트입니다.',
      };

      // 2. 반려식물 이미지 
      const formData = new FormData();
      formData.append("profileYn", "Y");
      formData.append("image", this.selectedFile);
      formData.append("cltvtActJson", JSON.stringify(cltvtActJson));
      
      axios
          .post("/api/file/uploadImage", formData)
          .then((response) => {
              console.log(response.status);
              alert('반려식물 프로필 사진이 변경되었습니다.');
              this.modalClose(true);
          })
          .catch((err) => {
              console.log("uploadImage post error");
              console.log(err);
          });

          // window.location.href = "/menu2_3";
  },
    onFileSelected: function (e) {
      // 1. 첨부할 파일을 가져옴.
      this.selectedFile = e.target.files[0];
      ///// validation (이미지인지? 사이즈 제한? )

      // 2. 파일을 base64 로 전환
      var reader = new FileReader();
      reader.readAsDataURL(this.selectedFile);

      // 3. 읽은 파일을 소스단에 출력
      reader.onload = () => {
          // console.log(reader.result);   // base64 object
          this.imageSrc = reader.result;
      };
      // this.$emit('input', this.selectedFile)
      console.log('selectedFile >>>>>>>> ', this.selectedFile);
    },
    imageSave(){
      // 2. 반려식물 이미지 
      const formData = new FormData();
      formData.append("image", this.selectedFile);
      console.log('selectedFile >>>>>>>> ', this.selectedFile);
      
      axios
            .post("/api/file", {formData: formData})
            // axios.post(this.router, resultJsonImp)
            .then((response) => {  
              console.log('extractSqlResult response : ', response);
            })
            .catch((error) => {
              console.log(error);
            });

          // window.location.href = "/menu2_3";
  },
    showContent(healingActivity) {
      this.healingActivity = healingActivity;
      this.showContentYn = true;
    },
  },
};
</script>